<template>
	<div class="file_btn" :class="{ file_btn_label: !$slots.default }">
		<label :for="compId">
			<slot>
				{{ $t(label) }}
			</slot>
		</label>
		<input :id="compId" class="file_input" type="file" @change="uploadFile" hidden :accept="_accept" :multiple="multiple" />
	</div>
</template>

<script>
	export default {
		props: {
			id: { type: String },
			label: { type: String, default: "selectFile" },
			accept: { type: String, default: "jpg,jpeg,png" },
			multiple: { type: Boolean, default: false },
			maxSize: { type: Number, default: 5 * 1024 * 1024 /* 5MB */ },
		},

		computed: {
			compId() {
				return this.id || "file_btn_" + this._uid;
			},
			_accept() {
				return this.accept.split(",").map((ext) => "image/" + ext.trim());
			},
		},

		methods: {
			uploadFile(e) {
				let file = e.target.files[0];

				if (file) {
					// Check file type
					if (this._accept.indexOf(file.type) === -1) {
						this.$emit("error", this.$t("fileNotSupported"));
						return;
					}

					// Check file size
					if (file.size > this.maxSize) {
						this.$emit("error", this.$t("fileSizeTooLarge"));
						return;
					}

					this.$emit("upload", file);
				}
			},
		},
	};
</script>

<style lang="scss">
	.file_btn {
		&.file_btn_label {
			text-align: center;
			user-select: none;
			label {
				display: inline-block;
				cursor: pointer;
				border: 1px solid #ccc;
				border-radius: 5px;
				background-color: #1a73e8;
				text-align: center;
				font-size: 14px;
				color: #fff;
				margin: 0 auto;
				box-shadow: 0 0.2rem 0.6rem 0 #00000026 !important;
				padding: 6px 12px;
				transition: all 0.2s ease-in-out;
				text-align: center;
				&:hover {
					background-color: #0f4d9e;
					color: #fff;
					box-shadow: 0 0.2rem 0.6rem 0 #00000026 !important;
				}
			}
		}
		input {
			display: none;
		}
	}
</style>
