<template>
	<b-modal id="LocationModal" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("location") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="body mb-3 my-5">
					<virtual-input :label="$t('location')" :is_active="isDropdownOpen">
						<template #icon_left>
							<BIcon icon="card-list" />
						</template>
						<template #default>
							<b-dropdown
								class="w-100"
								variant="link"
								:toggle-class="`text-decoration-none rounded-0 p-0 pb-2 box-shadow-none border-bottom w-100 d-flex align-items-center justify-content-between ${
									isDropdownOpen ? 'border-bottom-primary border-2' : ''
								}`"
								menu-class="p-0 shadow border-0 w-100"
								@shown="isDropdownOpen = true"
								@hidden="isDropdownOpen = false"
							>
								<template #button-content>
									<span class="text-dark">{{ location.name }}</span>
								</template>

								<b-dropdown-item link-class="p-3" v-for="(opt, i) in locationOptions" :key="i" @click="selectLocation(opt)">{{ opt.name }}</b-dropdown-item>
							</b-dropdown>
						</template>
					</virtual-input>

					<form-input
						label="Custom Text"
						required
						placeholder="Address, phone number, meeting link, etc."
						class="mt-3"
						v-if="isCustom"
						v-model="location.text"
						v-vuelidate="location.text"
					>
						<template #icon_left>
							<BIcon icon="text-paragraph" font-scale="1.4" scale="1.2" />
						</template>
					</form-input>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
					<b-btn type="submit" variant="primary" :disabled="!isValid">{{ $t("save") }}</b-btn>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import VirtualInput from "@/components/inputs/VirtualInput";

	import FormInput from "@/components/inputs/FormInput";

	import { required, requiredIf } from "vuelidate/lib/validators";

	import { validationMixin } from "vuelidate";

	export default {
		components: { VirtualInput, FormInput },

		props: { value: { type: Object } },

		mixins: [validationMixin],

		data() {
			return {
				isDropdownOpen: false,
				location: { type: "", name: this.$t("none"), text: "" },
			};
		},

		mounted() {
			this.location.type = this.value.type;
			this.location.name = this.value.name;
			this.location.text = this.value.text || "";
		},

		validations: {
			location: {
				name: { required },
				text: {
					required: requiredIf(function (nestedModel) {
						return nestedModel.type == "custom";
					}),
				},
			},
		},

		computed: {
			locationOptions() {
				return this.$store.state.invites.locationOptions;
			},

			isCustom() {
				return this.location.type == "custom";
			},

			isValid() {
				if (this.location.type == "custom") {
					if (this.location.text?.length < 1) return false;
				}

				return true;
			},
		},

		methods: {
			selectLocation(opt) {
				this.location = opt;
			},

			save() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$emit("input", {
					type: this.location.type,
					name: this.location.name,
					text: this.location.text,
				});

				this.$bvModal.hide("LocationModal");
			},

			shown() {
				this.location.type = this.value.type;
				this.location.name = this.value.name;
				this.location.text = this.value.text || "";
			},

			reset() {
				this.$bvModal.hide("LocationModal");
			},
		},
	};
</script>

<style></style>
