<template>
	<div class="textarea_form_wrapper" :class="{ active: isFoucsed, required, icon_left: $slots.icon_left }">
		<div class="icon_left">
			<slot name="icon_left" v-bind="{ active: isFoucsed }"></slot>
		</div>
		<div class="textarea_control">
			<label :for="id">{{ label }}</label>
			<div class="textarea_wrapper" ref="textareaWrapper">
				<textarea
					:id="id"
					class="form_textarea"
					ref="textarea"
					v-bind="inputProps"
					v-model="val"
					:type="type"
					:placeholder="placeholder"
					@focus="focus"
					@blur="blur"
					@change="onChange"
					@input="onInput"
					:required="required"
					rows="1"
				></textarea>
				<span class="hidden_text" ref="hiddenText">{{ value }}</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			inputProps: { type: Object },
			id: { type: String },
			label: { type: String },
			value: { type: String },
			type: { type: String, default: "text" },
			placeholder: { type: String, default: "" },
			required: { type: Boolean, default: false },
		},

		data() {
			return {
				isFoucsed: false,
			};
		},

		computed: {
			val: {
				get() {
					return this.value;
				},
				set(val) {
					this.$emit("input", val);
				},
			},
		},

		methods: {
			onChange(e) {
				this.val = e.target.value;

				this.$emit("change", e);
			},

			onInput(e) {
				this.val = e.target.value;

				this.$nextTick(() => {
					let hiddenText = this.$refs.hiddenText;

					let textareaWrapper = this.$refs.textareaWrapper;

					let textHeight = hiddenText.clientHeight;

					let textareaHeight = textareaWrapper.clientHeight;

					if (textHeight >= textareaHeight) {
						textareaWrapper.style.height = textHeight + "px";
					} else {
						textareaWrapper.style.height = "auto";
					}
				});

				this.$emit("input", e.target.value);
			},

			focus() {
				this.isFoucsed = true;
				this.$emit("focus");
			},

			blur() {
				this.isFoucsed = false;
				this.$emit("focus");
			},
		},
	};
</script>

<style lang="scss">
	.textarea_form_wrapper {
		display: flex;
		width: 100%;
		margin-bottom: 1rem;
		&.icon_left {
			.icon_left {
				width: 36px;
				top: 21px;
				position: relative;
				align-self: flex-start;
				overflow: hidden;
				svg {
					width: 1em;
					height: 1em;
				}
			}
		}
		.textarea_control {
			width: inherit;
			display: flex;
			flex-direction: column;
			label {
				display: block;
				font-size: 12px;
				color: #0000009a;
				margin-bottom: 0;
			}

			.textarea_wrapper {
				position: relative;
				display: inline-block;
				width: 100%;

				.form_textarea {
					width: 100%;
					height: 100%;
					padding: 0;
					border: none;
					border-bottom: 1px solid #ccc;
					box-sizing: border-box;
					font-size: 16px;
					font-weight: 400;
					color: #000000ef;
					background-color: #fff;
					transition: all 0.3s ease;
					outline: none;
					resize: none;
					overflow: hidden;
					&:hover,
					&:focus {
						border-color: transparent;
					}
				}

				.hidden_text {
					position: absolute;
					top: 0;
					left: 0;
					opacity: 0;
					pointer-events: none;
					visibility: hidden;
					height: auto;
					width: auto;
					word-break: break-all;
					font-size: 16px;
				}

				&:before {
					content: "";
					position: absolute;
					bottom: 0;
					width: 0;
					left: 50%;
					height: 2px;
					transition: 0.2s ease-out;
					background-color: #000000;
				}
			}
		}

		&.required {
			.textarea_control {
				label {
					&:after {
						content: "*";
						margin-left: 4px;
					}
				}
			}
		}

		& {
			.textarea_wrapper {
				&:hover {
					&:before {
						left: 0;
						width: 100%;
					}
					.form_textarea {
						border-color: transparent;
					}
				}
			}
			&.active,
			&[vuelidate-invalid="true"] {
				.textarea_wrapper {
					&:before {
						left: 0;
						width: 100%;
						background-color: var(--primary);
					}
					.form_textarea {
						border-color: transparent;
					}
				}
			}
		}

		&.active {
			.icon_left {
				color: var(--primary);
			}
			.textarea_control {
				label {
					color: var(--primary);
				}
			}
		}

		&[vuelidate-invalid="true"] {
			.icon_left {
				color: var(--danger);
			}
			.textarea_control {
				label {
					color: var(--danger);
				}
				.textarea_wrapper {
					&:before {
						background-color: var(--danger);
					}
				}
			}
		}
	}
</style>
