<template>
	<b-modal id="ImageModal" dialog-class="full_modal" hide-footer hide-header centered @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("addPhoto") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<div class="body pt-5">
				<b-nav fill>
					<b-nav-item active-class="border-bottom border-lg" v-for="tab in tabs" :key="tab.value" :active="tab.isActive" @click="setActiveTab(tab)">{{ tab.title }}</b-nav-item>
				</b-nav>
				<div class="tab mt-2">
					<template v-if="activeTab.value == 'unsplash'">
						<b-row cols="4" no-gutters>
							<b-col v-for="(d, i) in unsplashData" :key="i">
								<div class="image_thumb h-100" @click="setUnsplashImage(d)">
									<b-img thumbnail :src="d.urls.thumb" class="h-100 border-0" :alt="d.user.name" />
									<span class="image_title">{{ d.user.name }}</span>
								</div>
							</b-col>
						</b-row>
						<div class="load_more my-3">
							<b-button class="filter_btn active px-3 py-2 mx-auto font-weight-500" @click="loadMore">
								<BIcon icon="arrow-repeat" class="mr-2" />
								<div class="fs-sm">{{ $t("loadNewPhotos") }}</div>
							</b-button>
						</div>
					</template>

					<template v-else-if="activeTab.value == 'upload'">
						<div class="pb-5 mb-5">
							<div class="fs-sm text-muted text-center my-4">{{ $t("uploadTabDesc") }}</div>
							<div class="text-danger fs-xs text-center mb-2" v-if="imageUpload.isInvalid">{{ imageUpload.errorMsg }}</div>
							<FileBtn @upload="uploadImage" @error="handleImageUploadError" />
						</div>
					</template>

					<template v-else-if="activeTab.value == 'link'">
						<div class="pb-3 text-center">
							<div class="fs-sm text-muted my-4">{{ $t("linkTabDesc") }}</div>
							<b-form @submit.prevent="submitURLImage">
								<form-input
									dir="ltr"
									type="url"
									:label="$t('imageUrl')"
									placeholder="ex. https://www.example.com/image.jpg"
									v-model="imageURL.url"
									:is_invalid="imageURL.isInvalid"
									:errorMsg="imageURL.errorMsg"
								/>
								<b-btn type="submit" variant="primary" class="mt-4">{{ $t("save") }}</b-btn>
							</b-form>
						</div>
					</template>
				</div>
			</div>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";

	import { validationMixin } from "vuelidate";

	import FileBtn from "@/components/FileBtn";

	export default {
		components: { FormInput, FileBtn },

		mixins: [validationMixin],

		data() {
			return {
				tabs: [
					{ title: "Unsplash", value: "unsplash", isActive: true },
					{ title: this.$t("upload"), value: "upload", isActive: false },
					{ title: this.$t("link"), value: "link", isActive: false },
				],
				imageURL: { isInvalid: false, errorMsg: "", url: "" },
				imageUpload: { isInvalid: false, errorMsg: "", image: null },
			};
		},

		computed: {
			activeTab() {
				return this.tabs.find((tab) => tab.isActive);
			},

			unsplashData() {
				return this.$store.state.unsplash.data;
			},

			isValidImageURL() {
				if (!this.imageURL.url) return false;

				let isValidUrl =
					/^(?:(?:https?|ftp):\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,}))\.?)(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(
						this.imageURL.url
					);

				if (!isValidUrl) return false;

				return true;
			},
		},

		watch: {
			"imageURL.url"() {
				if (this.isValidImageURL) {
					this.imageURL.isInvalid = false;
					this.imageURL.errorMsg = "";
				} else {
					this.imageURL.isInvalid = true;
					this.imageURL.errorMsg = this.$t("enterValidUrl");
				}
			},
		},

		methods: {
			setActiveTab(tab) {
				this.tabs.forEach((t) => {
					t.isActive = t.value === tab.value;
				});
			},

			submitURLImage() {
				if (!this.isValidImageURL) {
					this.imageURL.isInvalid = true;
					this.imageURL.errorMsg = this.$t("enterValidUrl");
					return;
				}

				this.$emit("input", this.imageURL.url);
				this.$bvModal.hide("ImageModal");
			},

			uploadImage(file) {
				this.imageUpload.isInvalid = false;
				this.imageUpload.errorMsg = "";
				this.imageUpload.image = file;

				this.$emit("input", file);
				this.$bvModal.hide("ImageModal");
			},

			setUnsplashImage(image) {
				this.$emit("input", image.urls.regular);
				this.$bvModal.hide("ImageModal");
			},

			handleImageUploadError(error) {
				this.imageUpload.isInvalid = true;
				this.imageUpload.errorMsg = error;
			},

			loadMore() {},

			shown() {
				// this.$store.dispatch("unsplash/getUnsplashData");
			},

			reset() {
				this.$bvModal.hide("ImageModal");

				setTimeout(() => {
					this.tabs.forEach((tab) => {
						tab.isActive = tab.value === "unsplash";
					});
					this.imageURL = { isInvalid: false, errorMsg: "", url: "" };
					this.imageUpload = { isInvalid: false, errorMsg: "", image: null };
				}, 300);
			},
		},
	};
</script>

<style lang="scss">
	.nav-link {
		color: #333;
		&.active {
			border-bottom: 2px solid var(--primary);
			color: var(--primary);
		}
	}
	.image_thumb {
		position: relative;
		max-height: 66px;
		cursor: pointer;
		img {
			object-fit: cover;
			width: 100%;
		}
		.image_title {
			position: absolute;
			width: calc(100% - 8px);
			left: 4px;
			bottom: 5px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			color: white;
			background-image: linear-gradient(#00000066 10%, #000);
			font-size: 10px;
			padding: 2px 2px;
			opacity: 0;
			transition: opacity 0.1s ease-in-out;
		}
		&:hover {
			.image_title {
				opacity: 1;
			}
		}
	}
	.image_upload_wrapper {
		background-color: #fafafa;
		position: relative;
		border-radius: 4px;
		padding-bottom: calc(100% / 3);
		overflow: hidden;
		.btns {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;

			.btn_icon {
				svg {
					fill: rgb(79, 79, 79) !important;
				}
				&:hover {
					background-color: #00000022;
				}
			}
			&.has_review {
				.btn_icon {
					svg {
						fill: #fff !important;
					}
					&:hover {
						background-color: #0000003d;
					}
				}
			}
		}
		img {
			position: absolute;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
</style>
