<template>
	<b-modal id="URLModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("setCustomUrl") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="body mb-3 my-5">
					<div class="d-flex align-items-center" dir="ltr">
						<span class="text-muted">https://maw3d.net/tersawy/</span>
						<form-input required placeholder="30min" class="" v-model="url" v-vuelidate.noDirty="url" :inputProps="{ style: 'padding: 20px 0 7px' }"> </form-input>
					</div>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
					<b-btn type="submit" variant="primary" :disabled="$v.$invalid">{{ $t("save") }}</b-btn>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";

	import { validationMixin } from "vuelidate";

	export default {
		components: { FormInput },

		props: { value: { type: String } },

		mixins: [validationMixin],

		data() {
			return {
				url: "",
			};
		},

		mounted() {
			this.url = this.value;
		},

		validations: {
			url: {
				required: (v) => !!v,
				valid: (v) => /(^[a-zA-Z0-9]+$)|(^[a-zA-Z0-9]+(_|-)[a-zA-Z0-9]+$)/.test(v),
			},
		},

		methods: {
			save() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$emit("input", this.url);

				this.$bvModal.hide("URLModal");
			},

			shown() {
				this.url = this.value;
			},

			reset() {
				this.$bvModal.hide("URLModal");
			},
		},
	};
</script>

<style></style>
